@use "../system/mixins" as *;


.section-skills {
    @include fullwidth-bg(var(--ui-secondary));
    padding: {
        block-start: var(--gap-section-ver);
        block-end: var(--gap-section-ver);
    };
    scroll-margin-block-start: var(--gap-section-ver);

    &__title-box {
        margin: 0;
        margin-block-end: 8rem;
        padding: 0;
        color: var(--ui-primary);
    }

    &__quote {
        display: flex;
        flex-direction: column;
        row-gap: 1.2rem;
        font-size: 3.2rem;
    }

    &__quote-text {
        width: auto;
        max-width: 75rem;
        margin: 0;
        margin-inline-start: auto;
        padding: 0;
        font-style: italic;
    }

    &__quote-auth {
        font-style: italic;
        text-align: right;
    }

    &__list {
        @include reset-list;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(var(--w-card-min), 1fr));
        column-gap: 2.4rem;
        row-gap: 4.8rem;
    }


    /*
        === Media ===
    */
    @include media(w1024) {
        &__title-box {
            display: flex;
            flex-direction: column;
            row-gap: 2.4rem;
        }

        &__quote {
            font-size: 2.4rem;
        }
    }
}
